<template>
  <div class="content">
    <p>Pomůcký vhodné od:</p>
    <h5>{{ ageFrom }} {{ inflect(ageFrom) }}</h5>
    <div class="slider-wrapper">
      <VueSlider
        v-model="temp"
        :direction="rtl"
        :min="marks[0]"
        :max="marks[marks.length - 1]"
        :marks="marks"
        :included="true"
        :dotSize="20"
        :process="process"
      ></VueSlider>
    </div>
  </div>
</template>

<script>
import VueSlider from 'vue-slider-component';
import 'vue-slider-component/theme/antd.css';

export default {
  name: 'FilterAge',
  components: { VueSlider },

  data() {
    return {
      marks: [1, 1.5, 2, 3, 4, 5, 6, 7, 8, 10],
      process: (dotsPos) => [[100, dotsPos[0]]],
    };
  },
  props: ['ageFrom'],
  computed: {
    temp: {
      get() {
        return this.ageFrom;
      },
      set(newValue) {
        this.$emit('update:ageFrom', newValue);
      },
    },
  },
  methods: {
    inflect(ageFrom) {
      if (ageFrom < 2) {
        return 'roku';
      }
      return 'let';
    },
  },
};
</script>

