<template>
  <div class="overlay" v-if="isOpen" @click="$emit('close')">
    <div class="dialog" @click.stop>
      <div class="empty-cart" v-if="cart.totalQty == 0">
        Váš košík je prázdný
      </div>
      <div class="item" v-for="item in cart.items" :key="item.title">
        <div class="title">{{ item.title }}</div>
        <div class="others">
          <span>{{ item.qty }} ks</span>
          <span class="price">{{ item.price / item.qty }},- Kč</span>
          <SvgClose
            :size="20"
            @click="removeFromCart(item.itemIds[0])"
          ></SvgClose>
        </div>
      </div>

      <hr />
      <div class="bottom">
        <h5>Celkem: {{ cart.totalPrice }} Kč</h5>
        <button @click="confirmOrder">Objednat</button>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint no-underscore-dangle: ["error", { "allow": ["_id"] }] */

import SvgClose from '@/components/SvgClose.vue';
// import ApiService from '@/services/ApiService';

export default {
  name: 'CartPreview',
  components: { SvgClose },
  props: ['isOpen', 'cart'],

  data() {
    return {};
  },
  methods: {
    removeFromCart(itemId) {
      this.emitter.emit('removeFromCart', { cartId: this.cart._id, itemId });
    },
    confirmOrder() {
      console.log('Order cornfirmed');
      this.$emit('close');
      this.emitter.emit('Order cornfirmed');
    },
  },
};
</script>

