import axios from 'axios';

const baseURL = process.env.VUE_APP_API_URL;
// const baseURL = 'http://192.168.1.163:5000';
// const baseURL = 'http://localhost:5000';

const apiClient = axios.create({
  baseURL,
  headers: {
    Accept: 'application/json',
    'Content-Type': 'application/json',
  },
});

// apiClient.interceptors.request.use((request) => {
//   console.log('Starting Request', JSON.stringify(request, null, 2));
//   return request;
// });

export default {
  getNewCart() {
    return apiClient.get('/new-cart');
  },
  getListOfAids(filter) {
    // console.log('ApiService: ', { kategories });
    return apiClient.post('/list-of-aids', filter);
  },
  addToCart(cartId, itemId) {
    return apiClient.post(`/cart/${cartId}`, { itemId });
  },
  getCart(cartId) {
    return apiClient.get(`/cart/${cartId}`);
  },
  removeFromCart(cartId, itemId) {
    return apiClient.delete(`/cart/${cartId}`, { data: { itemId } });
  },
  submitOrder(cartId, formData) {
    return apiClient.post('/orders/new', { cartId, formData });
  },
  kategoriesCounts(filter) {
    return apiClient.post('/filter/kategories/counts', filter);
  },
  getTotalCount(filter) {
    return apiClient.post('/filter/count', filter);
  },
};
