<template>
  <div class="kategory-wrapper">
    <div class="kat-items-wrapper">
      <div
        v-for="(count, kategory, index) in categoriesCounts"
        :key="kategory"
        class="kategory-item"
      >
        <input
          type="checkbox"
          :id="'checkbox' + index"
          :value="kategory"
          v-model="checked"
          :disabled="count === 0 && !checked.includes(kategory)"
        />
        <label :for="'checkbox' + index"
          >{{ kategory }} {{ `(${count})` }}</label
        >
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'FilterKategories',
  computed: {
    checked: {
      get() {
        return this.selected;
      },
      set(newValue) {
        this.$emit('update:selected', newValue);
      },
    },
  },
  props: ['selected', 'categoriesCounts'],
};
</script>

