<template>
  <MyModal :isOpen="isOpen === 'aidDetails'" :aid="aid" @close="handleClose">
  </MyModal>
  <CartPreview
    :isOpen="isOpen === 'cartPreview'"
    :cart="cart"
    @close="handleClose"
    @minus="minus"
  ></CartPreview>
</template>

<script>
import MyModal from '@/components/MyModal.vue';
import CartPreview from '@/components/CartPreview.vue';

export default {
  name: 'RootModal',
  props: ['cart'],
  components: { MyModal, CartPreview },
  data() {
    return {
      isOpen: null,
      aid: null,
      cartContent: {},
    };
  },
  created() {
    this.emitter.on('openModal', (aid) => {
      document.body.style.overflow = 'hidden';
      this.isOpen = 'aidDetails';
      this.aid = aid;
    });
    this.emitter.on('cartPreview', (cartContent) => {
      // document.body.style.overflow = 'hidden';
      console.log('otevírám preview');
      this.isOpen = 'cartPreview';
      this.cartContent = cartContent;
    });
    document.addEventListener('keyup', this.handleKeyup);
  },
  beforeUnmount() {
    document.removeEventListener('keyup', this.handleKeyup);
  },
  methods: {
    handleClose() {
      this.isOpen = null;
      document.body.style.overflow = 'auto';
    },
    handleKeyup(e) {
      if (e.keyCode === 27) this.handleClose();
    },
    addToCart() {
      console.log('Now added to Cart!!');
    },
    minus(item) {
      console.log('minus');
      console.log({ item });
      this.cartContent.totalPrice -= item.price;
      this.cartContent.totalQty -= item.qty;
    },
  },
};
</script>
