<template>
  <div class="content">
    <p>Pomůcký v cenovém rozmezí:</p>
    <h5>{{ priceRange[0] }} - {{ priceRange[1] }}</h5>

    <div class="slider-wrapper">
      <VueSlider
        v-model="temp"
        :min="100"
        :max="500"
        :marks="true"
        :interval="50"
        :dotSize="20"
      ></VueSlider>
    </div>
  </div>
</template>

<script>
import VueSlider from 'vue-slider-component';
import 'vue-slider-component/theme/antd.css';

export default {
  name: 'FilterPrice',
  components: { VueSlider },
  props: ['priceRange'],
  computed: {
    temp: {
      get() {
        return this.priceRange;
      },
      set(newValue) {
        this.$emit('update:priceRange', newValue);
      },
    },
  },
};
</script>

