<template>
  <div class="overlay" v-if="isOpen">
    <div class="dialog">
      <button class="close" @click="$emit('close')">
        <img
          src="https://s2.svgbox.net/materialui.svg?ic=close&color=000"
          width="25"
          height="25"
        />
      </button>
      <div class="img-container2">
        <img class="aid" :src="aid.img" alt="" />
      </div>
      <div class="product-info">
        <h1>{{ aid.title }}</h1>
        <div>
          <h5>Popis:</h5>
          <p>{{ aid.description }}</p>
          <h5>Naše tipy:</h5>
          <p>{{ aid.tips }}</p>
        </div>
        <button @click="addToCart()">Do košíku</button>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint no-underscore-dangle: ["error", { "allow": ["_id"] }] */

export default {
  name: 'MyModal',
  props: ['isOpen', 'aid'],

  methods: {
    addToCart() {
      const item = {
        aidType: this.aid._id,
        id: this.aid.inStock[0]._id,
      };
      console.log('emituji addToCart');
      this.emitter.emit('addToCart', item);
      this.$emit('close');
    },
  },
};
</script>

