<template>
  <div class="home">
    <FilterRibbon @applyFilter="applyFilter($event)"></FilterRibbon>
    <div class="feed">
      <ItemContainer
        v-for="aid in aids"
        :key="aid.id"
        :aid="aid"
      ></ItemContainer>
    </div>
  </div>
</template>

<script>
/* eslint no-underscore-dangle: ["error", { "allow": ["_id"] }] */
import ApiService from '@/services/ApiService';

import FilterRibbon from '@/components/FilterRibbon.vue';
import ItemContainer from '@/components/ItemContainer.vue';

export default {
  name: 'Home',
  components: { FilterRibbon, ItemContainer },
  props: ['rootCart'],
  data() {
    return {
      aids: [],
      initialFilter: {
        checkedCategories: [],
        priceRange: [100, 500],
        ageFrom: 1,
      },
    };
  },
  computed: {
    cart: {
      get() {
        return this.rootCart;
      },
      set(newValue) {
        this.$emit('update:rootCart', newValue);
      },
    },
  },
  methods: {
    getListOfAids(filter) {
      ApiService.getListOfAids(filter)
        .then(({ data }) => {
          this.aids = data;
        })
        .catch((err) => {
          console.error(err);
        });
    },
    addToCart(item) {
      console.log('i have recived to cart command');
      ApiService.addToCart(this.cart._id, item.id)
        .then(({ data }) => {
          this.cart = data.updatedCart;
          const changedAid = this.aids.find(
            (aid) => aid._id === data.updatedAidType._id,
          );
          changedAid.inStock = data.updatedAidType.inStock;
        })
        .catch((err) => {
          console.error(err);
        });
    },
    removeFromCart({ cartId, itemId }) {
      ApiService.removeFromCart(cartId, itemId)
        .then(({ data }) => {
          console.log({ data });
          this.cart = data.updatedCart;
          const changedAid = this.aids.find(
            (aid) => aid._id === data.updatedAidType._id,
          );
          changedAid.inStock = data.updatedAidType.inStock;
        })
        .catch((err) => {
          console.error(err);
        });
    },
    applyFilter(filter) {
      console.log('Filter byl změněn', filter);
      this.getListOfAids(filter);
    },
  },
  created() {
    this.getListOfAids(this.initialFilter);
  },
  mounted() {
    this.emitter.on('addToCart', this.addToCart);
    this.emitter.on('removeFromCart', this.removeFromCart);
  },
};
</script>

