<template>
  <div class="filter-wrapper">
    <div
      @click="toggleDropdown"
      class="filter-dropdown"
      :class="{ open: isOpen }"
    >
      Nastavení filtrování pomůcek
    </div>
    <div v-if="isOpen" class="kategory-wrapper">
      <FilterMenu
        :items="items"
        :activeIndex="active"
        @indexChanged="active = $event"
      />
      <FilterKategories
        v-if="active === 0"
        v-model:selected="filter.checkedCategories"
        :categoriesCounts="categoriesCounts"
      />
      <FilterAge
        v-if="active === 1"
        v-model:ageFrom="filter.ageFrom"
      ></FilterAge>
      <FilterPrice
        v-if="active === 2"
        v-model:priceRange="filter.priceRange"
      ></FilterPrice>
      <button :disabled="totalCount === 0" @click="submitFilter">
        {{ inflect(totalCount) }}
      </button>
      <!-- <button class="disabled" v-else>Nelze nic zobrazit</button> -->
    </div>
  </div>
</template>

<script>
import ApiService from '@/services/ApiService';
import FilterMenu from '@/components/FilterMenu.vue';
import FilterKategories from '@/components/FilterKategories.vue';
import FilterAge from '@/components/FilterAge.vue';
import FilterPrice from '@/components/FilterPrice.vue';

export default {
  name: 'FilterRibbon',
  components: {
    FilterMenu,
    FilterKategories,
    FilterAge,
    FilterPrice,
  },
  data() {
    return {
      isOpen: false,
      items: ['Kategorie', 'Věk', 'Cena'],
      totalCount: 0,
      active: 0,
      categoriesCounts: {},
      filter: {
        checkedCategories: [],
        priceRange: [100, 500],
        ageFrom: 1,
      },
    };
  },
  watch: {
    filter: {
      deep: true,
      handler() {
        console.log('Filter has been changed!');
        this.getCategoriesCounts();
        this.getTotalCount();
      },
    },
  },
  methods: {
    submitFilter() {
      this.$emit('applyFilter', this.filter);
      this.isOpen = false;
    },
    toggleDropdown() {
      console.log('toggle now');
      this.isOpen = !this.isOpen;
    },
    inflect(count) {
      if (count === 0) {
        return 'Bohužel, nic neodpovídá';
      }
      if (count === 1) {
        return `Zobrazit ${count} pomůcku`;
      }
      if (count > 1 && count < 5) {
        return `Zobrazit ${count} pomůcky`;
      }
      return `Zobrazit ${count} pomůckek`;
    },
    getTotalCount() {
      ApiService.getTotalCount(this.filter)
        .then(({ data }) => {
          console.log('incoming: ', data);
          this.totalCount = data.count;
        })
        .catch((err) => {
          console.error(err);
        });
    },
    getCategoriesCounts() {
      ApiService.kategoriesCounts(this.filter)
        .then(({ data }) => {
          console.log('dataaa: ', data);
          if (Object.entries(this.categoriesCounts).length) {
            /* eslint-disable no-param-reassign */
            this.categoriesCounts = Object.keys(this.categoriesCounts).reduce(
              (obj, key) => {
                // prettier-ignore
                obj[key] = key in data.categoriesCounts ? data.categoriesCounts[key] : 0;
                return obj;
              },
              {},
            );
            /* eslint-enable no-param-reassign */
          } else {
            this.categoriesCounts = data.categoriesCounts;
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },
  },
  created() {
    this.getCategoriesCounts();
    this.getTotalCount();
  },
};
</script>

