<template>
  <MyHeader :totalQty="cart.totalQty" @openCartPreview="openCartPreview" />
  <router-view v-model:rootCart="cart" />
  <RootModal :cart="cart"></RootModal>
</template>

<script>
/* eslint no-underscore-dangle: ["error", { "allow": ["_id"] }] */
import ApiService from '@/services/ApiService';
import MyHeader from '@/components/MyHeader.vue';
import RootModal from '@/components/RootModal.vue';

export default {
  name: 'App',
  components: { MyHeader, RootModal },

  data() {
    return {
      cart: {},
    };
  },
  methods: {
    openCartPreview() {
      this.emitter.emit('cartPreview', this.cart);
    },
    getNewCart() {
      ApiService.getNewCart()
        .then(({ data }) => {
          localStorage.setItem('cartId', JSON.stringify(data._id));
          console.log({ data });
          this.cart = data;
        })
        .catch((err) => console.log(err));
    },

    finishOrder() {
      console.log('app comp: order confirmed');
      this.$router.push('cart');
    },
    setCartId() {
      const cartId = JSON.parse(localStorage.getItem('cartId'));
      if (cartId) {
        ApiService.getCart(cartId)
          .then(({ data: cart }) => {
            if (!cart) {
              localStorage.removeItem('cartId');
              this.getNewCart();
            } else {
              this.cart = cart;
            }
          })
          .catch((err) => {
            console.error(err);
          });
      } else {
        this.getNewCart();
      }
    },
  },
  created() {
    this.setCartId();
  },
  mounted() {
    this.emitter.on('Order cornfirmed', this.finishOrder);
    this.emitter.on('submitedOrder', (formData) => {
      ApiService.submitOrder(this.cart._id, formData);
    });
  },
};
</script>

